import Tasks from './TaskList.js';

export default class Algos {
  static storeInLS = (todo) => {
    const task = JSON.stringify(todo);
    localStorage.setItem('taskList', task);
  };

  static fetchFromLS = () => {
    let todoList;

    if (JSON.parse(localStorage.getItem('taskList')) === null) {
      todoList = [];
    } else {
      todoList = JSON.parse(localStorage.getItem('taskList'));
    }

    return todoList;
  };

  static assignIndex = (tasks) => {
    tasks.forEach((todo, i) => {
      todo.index = i + 1;
    });
  }

  static deleteTodo = (index) => {
    let todoList = this.fetchFromLS();
    const deletedTodo = todoList[index];

    todoList = todoList.filter((item) => item !== deletedTodo);

    this.assignIndex(todoList);
    this.storeInLS(todoList);
  };

  static updateTask = (descp, index) => {
    const todoList = this.fetchFromLS();
    const taskUpdate = todoList[index];

    todoList.forEach((item) => {
      if (item === taskUpdate) {
        item.description = descp;
      }
    });

    this.storeInLS(todoList);
    this.displayTodos();
  };

  static addTask = () => {
    document.querySelectorAll('.deleteIcon').forEach((btn) => btn.addEventListener('click', (e) => {
      e.preventDefault();
      let idx;
      if (btn.idx > 0) {
        idx = btn.idx - 1;
      } else {
        idx = 0;
      }
      this.deleteTodo(idx);
      this.displayTodos();
    }));
  }

  static editTodo = (idx) => {
    const todoList = this.fetchFromLS();
    const editedTodo = todoList[idx];

    document.getElementById('task-input').style.display = 'none';
    const taskEdit = document.getElementById('edit-input');
    taskEdit.value = editedTodo.description;
    taskEdit.setAttribute('idx', idx);
    document.querySelector('.edit-Todo-Form').style.display = 'block';
    taskEdit.focus();
  }

  static editTodoBtn = () => {
    document.querySelectorAll('.editIcon').forEach((btn) => btn.addEventListener('click', (e) => {
      e.preventDefault();
      let idx;
      if (btn.idx > 0) {
        idx = btn.idx - 1;
      } else {
        idx = 0;
      }
      this.editTodo(idx);
    }));
  }

  static editTodoClick = () => {
    document.querySelectorAll('.todoTask').forEach((todo) => todo.addEventListener('dblclick', (e) => {
      e.preventDefault();
      let idx;
      if (todo.idx > 0) {
        idx = todo.idx - 1;
      } else {
        idx = 0;
      }
      this.editTodo(idx);
    }));
  }

  static CreateTodo = ({ description, index }, currentStatus, iscompleted) => {
    const todo = document.createElement('li');
    todo.className = 'todo-item';
    todo.innerHTML = `
        <article class="todo_content">
          <input type="checkbox" id="${index}" name="" value="" class="checkbox" ${currentStatus}>
           <h3 id="${index}" class="todoTask ${iscompleted}">${description}</h3> <i></i>
        </article>
        <article class="todo-icons">
          <button class="edit-btn" id="${index}"><box-icon name='edit'></box-icon></button>
          <button class="trash-can" id="${index}"><box-icon type='solid' name='message-x'></box-icon></button>
        </article>
        `;

    return todo;
  }

  static displayTodos = () => {
    const todoList = this.fetchFromLS();
    const todoCont = document.getElementById('todo-list-items');
    todoCont.innerHTML = '';
    todoList.forEach((item) => {
      let currentStatus;
      let iscompleted;
      if (item.completed === true) {
        currentStatus = 'checked';
        iscompleted = 'completed';
      } else {
        currentStatus = '';
        iscompleted = '';
      }
      todoCont.append(this.createTodo(item, currentStatus, iscompleted));
    });
    this.addTask();
    this.editTodoBtn();
    this.editTodoClick();

    const event = new Event('updatedList');
    document.dispatchEvent(event);
  }

  static addTodoItems = (descp) => {
    const todoList = this.fetchFromLS();
    const idx = todoList.length + 1;
    const newTask = new Tasks(descp, idx);

    todoList.push(newTask);
    this.storeInLS(todoList);
    this.displayTodos();
  }
}